import { xai } from '@ai-sdk/xai'
import { streamText } from 'ai'
import { NextResponse } from 'next/server'
import getTools from './ai-tools'

const SYSTEM_PROMPT = `You are an AI for an electronic store.

You can recommend a product to the user.
You can get a list of products by using the getDevicesList tool.
`

export const maxDuration = 30

export async function POST(req) {
  let { messages } = await req.json()
  messages = messages
    .filter((msg) => msg.content.trim() !== '' && !msg.content.startsWith('Sorry, I encountered an error'))
    .map((msg) => ({
      role: msg.role,
      content: msg.content.trim(),
    }))

  try {
    const tools = await getTools()
    const result = streamText({
      model: xai('grok-2'),
      messages,
      system: SYSTEM_PROMPT,
      maxSteps: 20,
      tools,
    })
    return result.toDataStreamResponse()
  } catch (error) {
    console.error('Error in genAIResponse:', error)
    if (error instanceof Error && error.message.includes('rate limit')) {
      return { error: 'Rate limit exceeded. Please try again in a moment.' }
    }
    return {
      error: error instanceof Error ? error.message : 'Failed to get AI response',
    }
  }
}
